$sub-color: grey;
$main-color: black;

@mixin shrinkLabel {
    top: -14px;
    right: 5px;
    font-size: 12px;
    color: $main-color;
}

.group {
    position: relative;
    margin: 45px 0;
    text-align: right;
    .form-input {
        background: none;
        background-color: white;
        color: $sub-color;
        font-size: 18px;
        padding: 10px 10px 10px 10px;
        display: block;
        width: 100%;
        border: none;
        border-radius: 10px;
        box-shadow: 0 5px 5px 0 rgb(226, 226, 226);
        margin: 25px 0;
        text-align: right;
        &:focus {
            outline: none;
        }

        &:focus~.form-input-label {
            @include shrinkLabel();
        }
    }

    input[type='password'] {
        letter-spacing: 0.3em;
    }

    .form-input-label {
        color: $sub-color;
        font-size: 16px;
        font-weight: normal;
        position: absolute;
        pointer-events: none;
        right: 10px;
        top: 10px;
        transition: 300ms ease all;


        &.shrink {
            @include shrinkLabel();
        }
    }
}